/*
 * @Descripttion: 
 * metatitle
 * {
    path: '/order_mgt',
    name: 'orderMgt',
    meta: {
      title: '',
      scrollToTop: true,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/module/views/BuyAndSale/Order')
  }
 * 
 * {
    path: '/order_mgt/:type/:id',
    name: 'orderMgtTypeId',
    meta: {
      title: '',
      scrollToTop: true,
      keepAlive: false, // keepAlivefalsetabtruetab (true)
      usePathKey: true, // usePathKey true tab12 keepAlive: false3useFullPathKey
      useFullPathKey: true, // usePathKey true tab12 keepAlive: false3usePathKey
      dynamicTitle(to) { // tabtitle
        const {type, id} = to.params
        switch (type) {
        case 'detail':
          return `${id}`
        case 'reject':
          return `${id}`
        default:
          return ''
        }
      },
      breadcrumbList: [ // 
        {name: '', path: ''},
        {name: '', path: '/order_mgt'},
        {name: '', path: ''}
      ],
      commonModule: '@/module/views/BuyAndSale/Order/Detail' // component
    },
    component: () => import('@/module/views/BuyAndSale/Order/Detail')
  }
 * @version: 
 * @Author: liuminghao@benlai.com
 * @Date: 2020-10-19 15:05:56
 * @LastEditTime: 2020-11-27 14:32:35
 */
import AgentCamp from './AgentCamp'; // 
import BuyAndSale from './BuyAndSale'; 
import Qualification from './Qualification'; 
import Reconciliation from './Reconciliation'; 
import Report from './Report'; 
import Warehouse from './Warehouse'
const routersList = [
  ...AgentCamp,
  ...BuyAndSale,
  ...Qualification,
  ...Reconciliation,
  ...Report,
  ...Warehouse
]
export default routersList;



