import {Message, Notification} from 'element-ui'
/**
 * @description: 
 * @param {string} message 
 * @param {string} type  info/error/success/warning
 * @param {boolean} showClose 
 * @return {*}
 */
function message(message, type = 'info', showClose = true) {
  if(message) {
    Message({
      showClose,
      message,
      type
    })
  }
}
/**
 * @description: message
 */
function closeAllMsg() {
  Message.closeAll()
}

/**
 * @description: 
 * @param {string} title 
 * @param {string} message 
 * @param {string} type  info/error/success/warning
 * @param {boolean} showClose 
 * @param {string} position  top-right/top-left/bottom-right/bottom-left
 * @return {*}
 */
function notify(message, type = 'info', title = '', showClose = true, position = 'top-right') {
  if(message) {
    Notification({
      title,
      message,
      type,
      showClose,
      position
    });
  }
}
/**
 * @description: notify
 */
function closeAllNotify() {
  Notification.closeAll()
}

export default {
  message,
  notify,
  closeAllMsg,
  closeAllNotify
}



