// ==========********** type  **********==========
/**
 * @description: 
 * @param o {any} 
 * @param type {any} [String()Boolean(boolean)Number()Null(null)Undefined(undefined)Function()Object()Array()Date()RegExp()Symbol(Symbol)Promise(Promise)Set(Set)]
 * @return: {boolean} truefalse
 */
export function isCheckType(o, type) {
  return Object.prototype.toString.call(o).slice(8, -1) === type
}
/**
 * @description: false
 * @param o {any} 
 * @return: {boolean} truefalse
 */
export function isFalse(o) {
  if(!o || o === 'null' || o === 'undefined' || o === 'false' || o === 'NaN') return true
  else return false
}
export function isTrue(o) {
  return !isFalse(o)
}
/**
 * @description: 
 * @return: {String} navigator.userAgent  Agents = ["Android", "iPhone", "SymbianOS", "Windows Phone", "iPad", "iPod"]
 */
export function userAgent() {
  return navigator.userAgent
}
/**
 * @description: PC
 * @param {type} 
 * @return: {boolean} truefalse
 */
export function isPC () {
  let userAgentInfo = navigator.userAgent
  let Agents = ["Android", "iPhone", "SymbianOS", "Windows Phone", "iPad", "iPod"]
  let flag = true;
  for(let item of Agents) {
    if (userAgentInfo.indexOf(item) > 0) {
      flag = false
      break
    }
  }
  return flag
}
/**
 * @description: 
 * @param str {any} 
 * @param type {string} 
 * phone()
 * tel()
 * card()
 * pwd(8~28)
 * postal()
 * QQ(qq)
 * email()
 * money((2))
 * URL()
 * IP(IP)
 * number()
 * english()
 * chinese()
 * lower()
 * upper()
 * count()
 * bank()
 * @return: 
 */
export function checkStr (str, type) {
  switch(type) {
  case 'phone':
    return /^1[3|4|5|6|7|8|9][0-9]{9}$/.test(str);
  case 'tel':
    return /^(0\d{2,3}-\d{7,8})(-\d{1,4})?$/.test(str);
  case 'card':
    return /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(str);
  case 'pwd':
    return /^(?=.*[A-Z])(?=.*[a-z])(?=.*\d)[A-Za-z\d`~!@#$%^&*()_+-=,.?]{8,20}$/.test(str)
  case 'postal':
    return /[1-9]\d{5}(?!\d)/.test(str);
  case 'QQ':
    return /^[1-9][0-9]{4,9}$/.test(str);
  case 'email':
    return /^[\w-]+(\.[\w-]+)*@[\w-]+(\.[\w-]+)+$/.test(str);
  case 'money':
    return /^\d*(?:\.\d{0,2})?$/.test(str);
  case 'money4':
    return /^\d*(?:\.\d{0,4})?$/.test(str);
  case 'URL':
    // eslint-disable-next-line no-useless-escape
    return /(http|ftp|https):\/\/[\w\-_]+(\.[\w\-_]+)+([\w\-\.,@?^=%&:/~\+#]*[\w\-\@?^=%&/~\+#])?/.test(str);
  case 'number':
    return /^[0-9]*$/.test(str);
  case 'english':
    return /^[a-zA-Z]+$/.test(str);
  case 'chinese':
    return /^[\\u4E00-\\u9FA5]+$/.test(str);
  case 'lower':
    return /^[a-z]+$/.test(str);
  case 'upper':
    return /^[A-Z]+$/.test(str);
  case 'count':
    return /\w{1,20}$/.test(str);
  case 'bank':
    return /^([1-9]{1})(\d{15}|\d{16}|\d{18})$/.test(str);
  default:
    return false;
  }
}
/**
 * @description: 
 * @param sId {type} id
 * @return: 
 */
export function isCardID(sId) {
  if (!/(^\d{15}$)|(^\d{17}(\d|X|x)$)/.test(sId)) {
    console.log('')
    return false
  }
  //
  let aCity = {11: "", 12: "", 13: "", 14: "", 15: "", 21: "", 22: "", 23: "", 31: "", 32: "", 33: "", 34: "", 35: "", 36: "", 37: "", 41: "", 42: "", 43: "", 44: "", 45: "", 46: "", 50: "", 51: "", 52: "", 53: "", 54: "", 61: "", 62: "", 63: "", 64: "", 65: "", 71: "", 81: "", 82: "", 91: ""};
  if(!aCity[parseInt(sId.substr(0, 2))]) { 
    console.log('')
    return false
  }
  // 
  let sBirthday = `${sId.substr(6, 4)}-${Number(sId.substr(10, 2))}-${Number(sId.substr(12, 2))}`.replace(/-/g, "/")
  let timeStamp = new Date(sBirthday)
  if(sBirthday !== `${timeStamp.getFullYear}/${timeStamp.getMonth + 1}/${timeStamp.getDate}`) {
    console.log('')
    return false
  }
  // 
  let sum = 0
  let weights = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2]
  let codes = "10X98765432"
  for (let i = 0; i < sId.length - 1; i++) {
    sum += sId[i] * weights[i];
  }
  let last = codes[sum % 11]; // 
  if (sId[sId.length - 1] !== last) { 
    console.log('')
    return false
  }
  return true
}





