const AgentCamp = [
  {
    path: '/sale_mgt',
    name: 'saleMgt',
    meta: {
      title: '',
      scrollToTop: true,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/module/views/AgentCamp/SaleMgt')
  },
  {
    path: '/sale_mgt/add',
    name: 'saleMgtAdd',
    meta: {
      title: '',
      scrollToTop: true,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: '/sale_mgt'},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/module/views/AgentCamp/SaleMgt/Add')
  },
  {
    path: '/sale_mgt/:type/:id',
    name: 'saleMgtTypeId',
    meta: {
      title: '',
      scrollToTop: true,
      keepAlive: false,
      dynamicTitle(to) {
        const {type} = to.params
        switch (type) {
        case 'see':
          return ``
        case 'edit':
          return ``
        default:
          return ''
        }
      },
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: '/sale_mgt'},
        {name: '', path: ''}
      ],
      commonModule: '@/module/views/AgentCamp/SaleMgt/Edit'
    },
    component: () => import('@/module/views/AgentCamp/SaleMgt/Edit')
  },
  {
    path: '/con_order',
    name: 'conOrder',
    meta: {
      title: '',
      scrollToTop: true,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/module/views/AgentCamp/ConOrder')
  },
  {
    path: '/con_order/detail/:type/:id/:storeNo',
    name: 'conOrderDetailTypeId',
    meta: {
      title: '',
      scrollToTop: true,
      keepAlive: false,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: '/con_order'},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/module/views/AgentCamp/ConOrder/Detail')
  },
  {
    path: '/con_order/print/:id/:storeNo',
    meta: {
      title: '',
      scrollToTop: true,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: '/order_mgt'},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/module/views/AgentCamp/ConOrder/Print')
  }
]

export default AgentCamp;



