const Qualification = [
  {
    path: '/sc_qua',
    name: 'supplierQua',
    meta: {
      title: '',
      scrollToTop: true,
      keepAlive: true,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/module/views/Qualification/ScQua')
  },
  {
    path: '/sc_qua/detail/:type/:id/:modelId/:supplierId',
    name: 'supplierQuaDetailId',
    meta: {
      title: '',
      scrollToTop: true,
      keepAlive: false,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: '/goods_qua'},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/module/views/Qualification/GoodsQua/Detail')
  },
  {
    path: '/sc_qua/edit/:type/:id/:modelId/:supplierId',
    name: 'supplierQuaEditId',
    meta: {
      title: '',
      scrollToTop: true,
      keepAlive: false,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: '/goods_qua'},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/module/views/Qualification/GoodsQua/Edit')
  },
  {
    path: '/goods_qua',
    name: 'goodsQua',
    meta: {
      title: '',
      scrollToTop: true,
      keepAlive: true,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/module/views/Qualification/GoodsQua')
  },
  {
    path: '/goods_qua/detail/:type/:id/:modelId/:supplierId',
    name: 'goodsQuaDetailId',
    meta: {
      title: '',
      scrollToTop: true,
      keepAlive: false,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: '/goods_qua'},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/module/views/Qualification/GoodsQua/Detail')
  },
  {
    path: '/goods_qua/edit/:type/:id/:modelId/:supplierId',
    name: 'goodsQuaEditId',
    meta: {
      title: '',
      scrollToTop: true,
      keepAlive: false,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: '/goods_qua'},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/module/views/Qualification/GoodsQua/Edit')
  }
]

export default Qualification;



