const BuyAndSale = [
  {
    path: '/order_mgt',
    name: 'orderMgt',
    meta: {
      title: '',
      scrollToTop: true,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/module/views/BuyAndSale/Order')
  },
  {
    path: '/order_mgt/detail/:type/:id/:storeNo',
    name: 'orderMgtDetailTypeId',
    meta: {
      title: '',
      scrollToTop: true,
      keepAlive: false,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: '/order_mgt'},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/module/views/BuyAndSale/Order/Detail')
  },
  {
    path: '/order_mgt/reject/:type/:id/:storeNo',
    name: 'orderMgtRejectTypeId',
    meta: {
      title: '',
      scrollToTop: true,
      keepAlive: false,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: '/order_mgt'},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/module/views/BuyAndSale/Order/Reject')
  },
  {
    path: '/order_mgt/print/:id/:storeNo',
    meta: {
      title: '',
      scrollToTop: true,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: '/order_mgt'},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/module/views/BuyAndSale/Order/Print')
  },
  {
    path: '/quote_mgt',
    name: 'quoteMgt',
    meta: {
      title: '',
      scrollToTop: true,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/module/views/BuyAndSale/QuoteMgt')
  },
  {
    path: '/quote_mgt/add',
    name: 'addQuote',
    meta: {
      title: '',
      scrollToTop: true,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: '/quoteMgt'},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/module/views/BuyAndSale/QuoteMgt/AddQuote')
  },
  {
    path: '/quote_mgt/:paperNo',
    name: 'detail',
    meta: {
      title: '',
      scrollToTop: true,
      keepAlive: false,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: '/quoteMgt'},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/module/views/BuyAndSale/QuoteMgt/QuoteDetail')
  },
  {
    path: '/fixprice',
    name: 'fixPrice',
    meta: {
      title: '',
      scrollToTop: true,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/module/views/BuyAndSale/FixPrice')
  },
  {
    path: '/fixprice/:confirmPaperNo',
    name: 'fixDetail',
    meta: {
      title: '',
      scrollToTop: true,
      keepAlive: false,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: 'fixprice'},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/module/views/BuyAndSale/FixPrice/Detail')
  }
]

export default BuyAndSale;



