const errorRouter = [
  {
    path: '/error',
    name: 'error',
    meta: {
      title: '',
      scrollToTop: true,
      cantCheck: true,
      breadcrumbList: [
        {name: '', path: '/'},
        {name: '', path: '/error'}
      ]
    },
    component: () => import('@/components/ErrorPage')
  },
  {
    path: '/noopt',
    name: 'noOpt',
    meta: {
      title: '',
      scrollToTop: true,
      cantCheck: true,
      breadcrumbList: [
        {name: '', path: '/'},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/components/NoOpt')
  }
]

export default {
  errorRouter
}




