const Report = [
  {
    path: '/sale_search',
    name: 'saleSearch',
    meta: {
      title: '',
      scrollToTop: true,
      keepAlive: true,
      uniqueId: '/sale_search',
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/module/views/Report/SaleSearch')
  },
  {
    path: '/sale_search/detail/:goodsNo',
    name: 'saleSearchDetailGoodsNo',
    meta: {
      title: '',
      scrollToTop: true,
      keepAlive: false,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: '/sale_search'},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/module/views/Report/SaleSearch/Detail')
  },
  {
    path: '/sale_sum',
    name: 'saleSum',
    meta: {
      title: '',
      scrollToTop: true,
      keepAlive: true,
      uniqueId: '/sale_sum',
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/module/views/Report/SaleSum')
  }
]

export default Report;



