const Warehouse = [
  {
    path: '/receiveMgt',
    name: 'receiveMgt',
    meta: {
      title: '',
      scrollToTop: true,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/module/views/Warehouse/Receive')
  },
  {
    path: '/receiveMgt/:paperNo',
    name: 'receiveDetail',
    meta: {
      title: '',
      scrollToTop: true,
      keepAlive: false,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: '/receiveMgt'},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/module/views/Warehouse/Receive/Detail')
  },
  {
    path: '/refundMgt',
    name: 'refundMgt',
    meta: {
      title: '',
      scrollToTop: true,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/module/views/Warehouse/Refund')
  },
  {
    path: '/refundMgt/:paperNo/:storeNo',
    name: 'refundDetail',
    meta: {
      title: '',
      scrollToTop: true,
      keepAlive: false,
      breadcrumbList: [
        {name: '', path: ''},
        {name: '', path: '/refundeMgt'},
        {name: '', path: ''}
      ]
    },
    component: () => import('@/module/views/Warehouse/Refund/Detail')
  }
]

export default Warehouse



